export default {
	props:['comments','goodId'],
	computed:{
		
	},
	methods: {
		open(){
			uni.navigateTo({
				url: '/pages/detail-comments/detail-comments?id='+this.goodId
			});
		},
		event(item2,index2,index){
			return uni.previewImage({
				current:index,
				urls:this.comments[index].imgList,
				indicator:"default"
			})
			
		}
	}
}
